@import './global/global';

.cu-testimonial {
  container-type: inline-size;
}

.cu-testimonial[class*="cu-colorscheme--"],
cu-testimonial-deck[class*="cu-colorscheme--"] .cu-testimonial {
  .cu-testimonial__content {
    padding: 0 var(--cu-ps0) var(--cu-vr0);
  }
}

@container (min-width: 500px) {
  .cu-testimonial[class*="cu-colorscheme--"],
  cu-testimonial-deck[class*="cu-colorscheme--"] .cu-testimonial {
    .cu-testimonial__content {
      padding: var(--cu-vr0) var(--cu-ps1) var(--cu-vr0) 0;
    }
  }
}

.cu-testimonial__inner {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--cu-vr1);
  align-items: center;
  justify-items: center;

  .cu-testimonial:has(.cu-testimonial__media) & {
    justify-items: unset;
  }
}

@container (min-width: 500px) {
  .cu-testimonial:has(.cu-testimonial__media) .cu-testimonial__inner {
    grid-template-columns: 40.3% 1fr;
    gap: var(--cu-vr2);
  }
}

.cu-testimonial__content {
  align-self: center;
  order: 2;
  position: relative;
  text-align: center;
  max-width: 45rem;

  .cu-testimonial:has(.cu-testimonial__media) & {
    text-align: left;

    &::before {
      content: '';
      display: block;
      width: 80px;
      height: 10px;
      background: var(--cu-colorscheme-accent);
      margin-bottom: var(--cu-vr1);
    }
  }
}

.cu-testimonial__quote {
  --cu-font-size: var(--cu-ms2);
  --cu-font-weight: 600;
  --cu-line-height: 1.3;
  --cu-font-family: "freight-text-pro", serif;
  margin-bottom: var(--cu-vr1);
  margin-top: -.5rem;
}

.cu-testimonial:has(.cu-testimonial__media) .cu-testimonial__quote  {
  --cu-font-weight: 400;
  --cu-line-height: 1.2;
}

@container (min-width: 500px) {
  .cu-testimonial__quote {
    margin-top: revert;
  }
}

@container (min-width: 700px) {
  .cu-testimonial__quote {
    --cu-font-size: var(--cu-ms4);
  }
}

.cu-testimonial__attribution {
  --cu-font-size: var(--cu-ms-4);
  --cu-font-weight: 700;
  opacity: .75;
  letter-spacing: 0.1em;
  line-height: 1.3;
  text-transform: uppercase;
}

.cu-testimonial__media {
  order: 1;

  img, video {
    display: block;
    object-fit: cover;
    width: 100%;
    height: 100%;
    aspect-ratio: 6 / 5;
  }
}

@container (min-width: 500px) {
  .cu-testimonial__media {
    img, video {
      aspect-ratio: unset;
    }
  }
}
